/*navbar start*/
#header-wrap{
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 9999;
}
#header-wrap>ul{
  display: table;
  width:100%;
  height: 10px;
}
#header-wrap>ul>li{
  display: table-cell;
  height: 100%;
}
#header-wrap>ul>li:nth-of-type(1){
  background: #f12f25;
}
#header-wrap>ul>li:nth-of-type(2){
  background: #ff9531;
}
#header-wrap>ul>li:nth-of-type(3){
  background: #efd21f;
}
#header-wrap>ul>li:nth-of-type(4){
  background: #aec534;
}
#header-wrap>ul>li:nth-of-type(5){
