}
.head-me{
  float: right;
    height: 32px;
    display: block;
    padding: 4px 0;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    cursor: pointer;
}
.head-me>img{
  display: block;
  height: 24px;
  line-height: 32px;
}
.top-bar{
  width: 100%;
  padding: 5px 0;
  background: #000;
  color: #fff;
  position: relative;
}
.bar-wrap{
  width: 90%;
  margin: 0 auto;
  height: 32px;
}
.top-title{
  width: 40%;
  text-align: left;
  float: left;
  height: 32px;
}
.top-title>a{
  display: inline-block;
  line-height: 32px;
  padding: 0;
  margin: 0;
}
.top-title>a>img{
  width: 32px;
  margin-top: -3px;
  height: auto;
}
.top-title>h2{
  display: inline;
  font-size: 24px;
  letter-spacing: 1px;
  padding-right: 10px;
  padding-left: 6px
}
.top-title>span{
  display: inline-block;
  font-size: 12px;
  color: #ccc;
  letter-spacing: 1px;
}
.bar-main{
  width: 60%;
  float: right;
  text-align: right;
}
.nav-wrap{
  width: 100%;
  height: 32px;
  /*position: relative;*/
  /*overflow: hidden;*/
}
.nav-wrap>nav{
  width: 100%;
  height: 100%;
  float: right;
}
.small-nav{
  display: none;
}
.nav-wrap>nav>ul{
  display: table;
  float: right;
}
.nav-wrap>nav>ul>li{
  display: table-cell;
}
.nav-wrap>nav>ul>li>a{
  color: #fff;
  padding: 0 10px;
  font-size: 16px;
  position: relative;
  line-height: 32px;
  font-family: '\5FAE\8F6F\96C5\9ED1';
  letter-spacing: 6px;
}
.nav-wrap>nav>ul>li>a:after{
  content: "*";
  display: block;
  right: -6px;
  font-size: 20px;
  line-height: 32px;
  color: #fff;
  font-size: 20px;
  position: absolute;
  top: -2px;
}
.navActive{
  color: #b4b4b4!important;
}





















