#writing{
	position: relative;
	overflow: hidden;
	padding:0;
}
#writing:before{
	content: '';
	display: block;
	background-size: auto 100%;
	position: absolute;
	left: 0;
	top: 0;
	width: 3600px;
	height: 100%;
	background: url(/static/img/writing-bg.dcf809d.jpg);
	background-position: bottom left;
	background-repeat: repeat-x;
	moz-animation: bg 60s linear infinite;
    -webkit-animation: bg 60s linear infinite;
    animation: bg 60s linear infinite;
    -moz-backface-visibility: hidden;
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
}
#writing{
}
.wr-content{
	position: relative;
	z-index: 99;
	padding: 30px 0;
	padding-bottom: 50px;
	width: 90%;
	margin: 0 auto;
}
.event_wrap{
	/*transform: rotate(-90deg);*/
	/*transform: ;*/
}
.wr-title{
	position: relative;
	z-index: 100;
	color: #fff;
}
.wr-content>ul{
	display: table;
}
.wr-content>ul>li{
	display: table-cell;
	width: 25%;
	padding: 1%;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	position: relative;
	left: 20px;
	top: 40px;
	opacity: 0;
	-webkit-transition: all 1s ease-in;
    transition: all 1s ease-in;
}
.wr-content>ul>li.fade{
	top: 0;
	left: 0;
	opacity: 1;
	-webkit-transition: all 1s ease-in;
    transition: all 1s ease-in;
}
.wr-content>ul>li:nth-of-type(even){
	-webkit-transition: all 1.5s ease-in;
    transition: all 1.5s ease-in;
}
.wr-img{
	width: 100%;
}
.w-link{
	display: block;
	width: 100%;
	height: 100%;
	-webkit-box-shadow: 2px 2px 2px #959595,4px 4px 2px #959595;
	        box-shadow: 2px 2px 2px #959595,4px 4px 2px #959595;
	position: relative;
	overflow: hidden;
}
.wr-img>img{
	width: 100%;
	-webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}
.w-txt{
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	color: #fff;
	/*padding: 10px;*/
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	background: #10090b;
	opacity: .8;
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}
.w-txt>h3{
	position: relative;
	top: -44px;
	/*z-index: 10;*/
	font-family: '\5FAE\8F6F\96C5\9ED1';
	font-size: 16px;
	letter-spacing: 1px;
	padding-top: 16px;
	padding-bottom: 12px;
	text-align: center;
	/*background: #10090b;*/
	opacity: .8; 
	color: #fff;
	background: url(data:image/gif;base64,);
	background-repeat: repeat-x;
}
.w-txt>p{
	line-height: 1.6;
	padding: 0 20px;
}
.w-link:hover .w-txt{
	top: 0;
	background: (0,0,0,.8);
}
.w-link:hover .w-txt>h3{
	top: 0;
	padding-top: 30px;
}
.w-link:hover .wr-img>img{
	-webkit-transform: scale(1.4);
    transform: scale(1.4);
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}
